  <v-container fluid>
    <v-row align="center" justify="center">
      <v-card max-width="75%" flat color="background">
        <v-row class="d-flex">
          <h1 class="mb-4">Sigue tu servicio</h1>
        </v-row>

        <MapTracingVue />

        <v-row align="center" justify="center">
          <v-col>
            <v-row class="d-flex align-center justify-start mt-4">
              <p>Hora mxima de llegada:</p>
            </v-row>
          </v-col>
          <v-col>
            <v-row class="d-flex align-center justify-end">
              <h1>{{ servicioData.deliveryTime.slice(0, 5) }} hrs</h1>
            </v-row>
          </v-col>
        </v-row>

        <v-row class="mb-6" align="center" justify="center">
          <v-progress-linear
            color="secondary"
            height="10"
            rounded
            :value="progesoData.percentage"
          ></v-progress-linear>
        </v-row>

        <!-- <v-row class="d-flex align-center justify-start mt-4">
          <p>
            <b>Llegando a:</b>
            Irolo 87 Zacahuitzco, Benito Juarez, CDMX, 03550 Interior 101
