        </v-row> -->

        <v-row class="d-flex">
          <h2 class="mb-4">Tu repartidor</h2>
        </v-row>

        <v-row align="center" justify="center">
          <v-col>
            <v-row align="center" justify="center">
              <v-avatar size="80">
                <img :src="servicioData.provider.urlPhoto" />
              </v-avatar>
            </v-row>
          </v-col>
          <v-col>
            <v-row align="center" justify="start">
              <h3>{{ servicioData.provider.shortName }}</h3>
            </v-row>
            <v-row align="center" justify="start">
              <p><b>Asistente:</b> {{ servicioData.provider.assistantName }}</p>
            </v-row>
            <v-row align="center" justify="start">
              <p><b>Compaa:</b> {{ servicioData.provider.company.name }}</p>
            </v-row>
            <v-row align="center" justify="start">
              <p>
                <b>Precio:</b> ${{
                  servicioData.provider.company.pricePerLiter
                }}
                Litro
              </p>
            </v-row>
          </v-col>
          <!-- <v-col>
            <v-row align="center" justify="center">
              <v-btn outlined>Llamar</v-btn>
            </v-row>
          </v-col> -->
        </v-row>

        <!-- <v-row align="center" justify="center">
          <v-btn color="primary" class="mb-6" text>Cancelar mi servicio</v-btn>
