<script>
import Axios from "axios";
import { mapFields } from "vuex-map-fields";
import Api from "../statics/apis";
import MapTracingVue from "../components/MapTracing.vue";

export default {
  computed: {
    ...mapFields([
      "seguirServicioNumberUuid",
      "coordenadasSigueServicio",
      "loader",
    ]),
  },
  data() {
    return {
      servicioData: {
        deliveryTime: "17:00:00",
      },
      progesoData: {},
    };
  },
  components: {
    MapTracingVue,
  },
  methods: {
    // toQualify() {
    //   this.$router.push({ name: "Qualify" });
    // },
    async getData() {
      this.loader = true;
      let api = Axios.get(
        Api.url + "/services/detail-customer/" + this.seguirServicioNumberUuid,
        {
          headers: {
            "x-api-key": Api.apiKey,
          },
        }
      )
        .then((response) => {
          this.servicioData = response.data;
          return Axios.get(
            Api.url +
              "/services/customer/progress/" +
              this.seguirServicioNumberUuid,
            {
              headers: {
                "x-api-key": Api.apiKey,
              },
            }
          );
        })
        .then((response) => {
          this.progesoData = response.data;
          this.getCoordenadas(response.data);
          this.loader = false;
        })
        .catch((error) => {
          console.error(error);
          this.loader = false;
          // this.$router.push({ name: "Home" });
        });
    },
    getCoordenadas(data) {
      this.coordenadasSigueServicio.coordenadasServicio.lat = parseInt(
        data.serviceCoordinates.latitude
      );
      this.coordenadasSigueServicio.coordenadasServicio.lng = parseInt(
        data.serviceCoordinates.longitude
      );

      this.coordenadasSigueServicio.coordenadasProveedor.lat = parseInt(
        data.providerCoordinates.latitude
      );
      this.coordenadasSigueServicio.coordenadasProveedor.lng = parseInt(
        data.providerCoordinates.longitude
      );
    },
  },
  mounted() {
    if (this.seguirServicioNumberUuid) {
      this.getData();
    } else {
      this.$router.push({ name: "Home" });
    }
  },
};
</script>

