<script>
import Axios from "axios";
import { mapFields } from "vuex-map-fields";
import MapConfirmServiceVue from "./MapConfirmService.vue";
import Api from "../statics/apis.js";
var mixpanel = require('mixpanel-browser');
mixpanel.init(Api.key);

export default {
  computed: {
    ...mapFields([
      "servicioSolicitado",
      "landingCounter",
      "pagoTarjetaMaximoFlag",
      "contadorDatos",
      "formOkBoy",
      "conoceMasModal",
      "showMessage",
      "message",
      "loader",
    ]),
    viewPromocode:function(){
      if(this.formOkBoy.tipoServicio === 'Estacionario'){
        return true;
      }
      return false;
    }
  },
  components: {
    MapConfirmServiceVue,
  },
  data() {
    return {
      codigoDescuentoFlag: false,
      codigoDescuento: null,
    };
  },
  methods: {
    toTracing() {
      this.getDataServices();
    },
    // clearData() {
    //   this.landingCounter = 1
    //   this.contadorDatos = 0
    //   this.formOkBoy = this.formOkBoyStatic
    // },
    changeAccount() {
      this.landingCounter = 1;
      this.contadorDatos = 0;
    },
    changeDate() {
      this.landingCounter = 2;
    },
    changePay() {
      this.landingCounter = 3;
    },
    changeDirection() {
      this.landingCounter = 1;
      this.contadorDatos = 2;
    },
    total() {
      let total = 0;
      let services = this.formOkBoy.cantidadGasCilindro;
      let descuento = 0;
      if(this.formOkBoy.tipoServicio === 'Estacionario'){
        descuento = this.formOkBoy.descuento;
        services = this.formOkBoy.cantidadGasEstacionario;
      }else{
        this.formOkBoy.descuento = 0;
        this.formOkBoy.promocode = "";
        this.formOkBoy.cuponServicio = "";
      }
      total =
        +services +
        +this.formOkBoy.comision -
        +descuento;
      return total;
    },
    uppercaseCodigo() {
      this.codigoDescuento = this.codigoDescuento.toUpperCase();
    },
    tipoPago() {
      // let tipoPago = "";
      switch (this.formOkBoy.formaPago) {
        case "Tarjeta":
          return "Pago con tarjeta";
          break;
        case "Terminal":
          return "Pago con terminal";
          break;
        case "Efectivo":
          return "Pago en efectivo";
          break;
      }
    },
    terminacionTarjeta() {
      let numTarjeta = this.formOkBoy.numeroTarjeta;
      return numTarjeta.substr(12, 5);
    },
    toConoceMas() {
      this.conoceMasModal = true;
    },
    getDescuento(codigoDescuento) {
      this.loader = true;
      let api = Axios.get(
        Api.url + "/services/promocodes/" + codigoDescuento + "/discount",
        {
          params: {
            phoneNumber: this.formOkBoy.numeroCelular,
          },
          headers: {
            "x-api-key": Api.apiKey,
          },
        }
      )
        .then((response) => {
          this.loader = false;
          this.codigoDescuentoFlag = true;
          this.formOkBoy.cuponServicio = codigoDescuento;
          this.formOkBoy.descuento = response.data.discount;
          this.showMessage = true;
          this.message = "Descuento de: $" + response.data.discount;
        })
        .catch((error) => {
          console.error(error);
          this.loader = false;
          this.codigoDescuentoFlag = false;
          this.formOkBoy.cuponServicio = "";
          this.formOkBoy.descuento = 0;
          this.showMessage = true;
          this.message = "Cdigo no valido.";
          this.codigoDescuento = null;
        });
    },
    getDataServices() {
      let DataServices = {
        amount: `${this.formOkBoy.montoTotalGas * 100}`,
        fee: `${(this.formOkBoy.comision || 0) * 100}`,
        discount: `${(this.formOkBoy.descuento || 0) * 100}`,
        serviceDate: this.formOkBoy.dateServices.toString(),
        paymentTypeId: this.formOkBoy.formaPagoID.toString(),
        promocode: this.formOkBoy.cuponServicio,
        customerData: {
          phoneNumber: this.formOkBoy.numeroCelular.toString(),
          name: this.formOkBoy.nombreCliente.toString(),
          deviceType: "web",
          address: this.formOkBoy.address,
        },
      };
      if (this.formOkBoy.tipoServicio === "Cilindro") {
        DataServices.serviceType = "c352aee9-4217-4dea-acde-7446fe7bc501";
      }
      if (this.formOkBoy.formaPago === "Tarjeta") {
        DataServices.card = this.formOkBoy.cardCrypto;
      }
      this.loader = true;
      let api = Axios.post(Api.url + "/services", DataServices, {
        headers: {
          "x-api-key": Api.apiKey,
        },
      })
        .then((response) => {
          this.loader = false;
          // this.$router.push({ name: "Home" });
          this.servicioSolicitado = true;
          mixpanel.track('Registro servicio.');
        })
        .catch((error) => {
          if (error.response.data.remainingAttempts === 0) {
            this.showMessage = true;
            this.message =
              "Por polticas de la empresa ya no tienes permitido pagos con tarjeta";
            this.landingCounter = 3;
            this.formOkBoy.formaPago = "Terminal";
            this.pagoTarjetaMaximoFlag = true;
          }
          if (error.response.data.message === "Payment refused") {
            this.showMessage = true;
            this.message = "Pago rechazado, intenta otro mtodo de pago";
            this.landingCounter = 3;
          } else {
            console.error(error);
          }
          this.loader = false;
        });
    },

    getComicion() {
      let api = Axios.get(Api.url + "/commission", {
        headers: {
          "x-api-key": Api.apiKey,
        },
      })
        .then((response) => {
          this.formOkBoy.comision = response.data.commission;
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  mounted() {
    this.getComicion();
  },
};

window.dataLayer = window.dataLayer || [];
window.dataLayer.push({
    'event': 'Pageview',
    'pagePath': 'https://okboy.app/summary',
    'pageTitle': 'Service Summary for Confirmation'
  });
</script>

