<script>
import { mapFields } from "vuex-map-fields";
import { JSEncrypt } from "jsencrypt";
import pubKey from "./../statics/publicKey";
import { mask } from "vue-the-mask";
import Api from "../statics/apis.js";
var mixpanel = require('mixpanel-browser');
mixpanel.init(Api.key);

export default {
  computed: {
    ...mapFields(["landingCounter", "formOkBoy", "pagoTarjetaMaximoFlag", "showMessage", "message"]),
  },
  directives: {
    mask,
  },
  data() {
    return {
      // panels: 0,
      nameRules: [
        (v) => !!v || "El nombre es requerido.", //
      ],
      cardNumberRules: [
        (v) => !!v || "El nmero de tarjeta es requerido.", //
        (v) => (v && v.length <= 16) || "El nmero debe ser de 16 dgitos",
        (v) => RegExp("[0-9]+$").test(v) || "Ingresa slo nmeros",
      ],
      mmaaRules: [
        (v) => !!v || "La vigencia es requerida.", //
        (v) => (v && v.length <= 5) || "El nmero debe ser de 4 dgitos",
        (v) => RegExp("[0-9]+$").test(v) || "Ingresa slo nmeros",
      ],
      cvvRules: [
        (v) => !!v || "El cvv es requerido.", //
        (v) => (v && v.length <= 3) || "El nmero debe ser de 3 dgitos",
        (v) => RegExp("[0-9]+$").test(v) || "Ingresa slo nmeros",
      ],
    };
  },
  methods: {
    next() {
      if (this.formOkBoy.panelSelected >= 0) {
        switch (this.formOkBoy.panelSelected) {
          case 0:
            this.formOkBoy.formaPago = "Tarjeta";
            if (this.$refs.form.validate()) {
              this.formOkBoy.formaPagoID = 3;
              this.encryptData();
              mixpanel.track('Click Pago Tarjeta');
              this.landingCounter++;
            }
            break;
          case 1:
            this.formOkBoy.formaPago = "Terminal";
            this.formOkBoy.formaPagoID = 2;
            mixpanel.track('Click Pago Terminal');
            this.landingCounter++;
            break;
          case 2:
            this.formOkBoy.formaPago = "Efectivo";
            this.formOkBoy.formaPagoID = 1;
            mixpanel.track('Click Pago Efectivo');
            this.landingCounter++;
            break;
        }

        // switch (this.formOkBoy.formaPago) {
        //   case "Tarjeta":
        //     if (this.$refs.form.validate()) {
        //       this.formOkBoy.formaPagoID = 3;
        //       this.encryptData();
        //       this.landingCounter++;
        //     }
        //     break;
        //   case "Terminal":
        //     this.formOkBoy.formaPagoID = 2;
        //     this.landingCounter++;
        //     break;
        //   case "Efectivo":
        //     this.formOkBoy.formaPagoID = 1;
        //     this.landingCounter++;

        //     break;
        // }
      } else {
        this.message = 'Selecciona un mtodo de pago.'
        this.showMessage = true
      }
    },
    back() {
      this.clearForm();
      this.landingCounter--;
    },
    clearForm() {
      this.$refs.form.reset();
    },
    encryptData() {
      let card = {
        number: "",
        cvv: "",
        name: "",
        expiration: "",
      };
      let encrypt = new JSEncrypt();
      encrypt.setPublicKey(pubKey.publicKey);
      card.number = encrypt.encrypt(this.formOkBoy.numeroTarjeta);
      card.cvv = encrypt.encrypt(this.formOkBoy.cvvTarjeta);
      card.name = encrypt.encrypt(this.formOkBoy.titularTarjeta);
      card.expiration = encrypt.encrypt(this.formOkBoy.validaTarjeta);
      this.formOkBoy.cardCrypto = card;
    },
  },
  watch: {
    pagoTarjetaMaximoFlag: function (val) {
      if (val) {
        this.clearForm();
      }
    },
  },
};

window.dataLayer = window.dataLayer || [];
window.dataLayer.push({
    'event': 'Pageview',
    'pagePath': 'https://okboy.app/pay',
    'pageTitle': 'Payment Service'
  });
</script>

